<script>
import api from '@/common/utils/api.js';
import AdVideoBanner from '@/common/components/ad-video-banner.vue';

export default {
	components: {
		AdVideoBanner
	},
	data() {
		return {
			currentTab: 0,
			tabs: [
				{ name: 'Âø´ÈÄíÊü•ËØ¢', icon: 'üì¶' },
				{ name: '‰ªäÊó•Ê≤π‰ª∑', icon: '‚õΩ' },
				{ name: 'Ê±áÁéáÊç¢ÁÆó', icon: 'üí±' },
				{ name: 'ÂΩ©Á•®ÂºÄÂ•ñ', icon: 'üé∞' }
			],
			// Âø´ÈÄíÊü•ËØ¢
			express: {
				trackingNo: '',
				phone: ''
			},
			expressResult: null,
			// Ê≤π‰ª∑Êü•ËØ¢
			oil: {
				province: ''
			},
			oilResult: null,
			// Ê±áÁéáÊü•ËØ¢
			forex: {
				base: 'CNY',
				target: 'USD',
				amount: '100'
			},
			forexResult: null,
			currencyList: ['CNY', 'USD', 'EUR', 'GBP', 'JPY', 'HKD', 'KRW', 'AUD', 'CAD', 'SGD', 'CHF', 'NZD', 'THB', 'MYR', 'INR'],
			currencyLabels: ['CNY(‰∫∫Ê∞ëÂ∏Å)', 'USD(ÁæéÂÖÉ)', 'EUR(Ê¨ßÂÖÉ)', 'GBP(Ëã±Èïë)', 'JPY(Êó•ÂÖÉ)', 'HKD(Ê∏ØÂ∏Å)', 'KRW(Èü©ÂÖÉ)', 'AUD(Êæ≥ÂÖÉ)', 'CAD(Âä†ÂÖÉ)', 'SGD(Êñ∞Âä†Âù°ÂÖÉ)', 'CHF(ÁëûÂ£´Ê≥ïÈÉé)', 'NZD(Êñ∞Ë•øÂÖ∞ÂÖÉ)', 'THB(Ê≥∞Èì¢)', 'MYR(È©¨Êù•Ë•ø‰∫öÊûóÂêâÁâπ)', 'INR(Âç∞Â∫¶Âç¢ÊØî)'],
			baseCurrency: 'CNY',
			targetCurrency: 'USD',
			baseCurrencyIndex: 0,
			targetCurrencyIndex: 1,
			// ÂΩ©Á•®Êü•ËØ¢
			lotteryTypes: ['ÂèåËâ≤ÁêÉ', 'Â§ß‰πêÈÄè'],
			lotteryTypeIndex: 0,
			lotteryType: 'ssq',
			lotteryResult: null
		};
	},
	methods: {
		switchTab(index) {
			this.currentTab = index;
			// ÂàáÊç¢tabÊó∂Ê∏ÖÁ©∫ÁªìÊûú
			this.expressResult = null;
			this.oilResult = null;
			this.forexResult = null;
			this.lotteryResult = null;
		},
		onBaseChange(e) {
			const index = parseInt(e.detail.value);
			this.baseCurrencyIndex = index;
			this.baseCurrency = this.currencyList[index];
			this.forex.base = this.baseCurrency;
		},
		onTargetChange(e) {
			const index = parseInt(e.detail.value);
			this.targetCurrencyIndex = index;
			this.targetCurrency = this.currencyList[index];
			this.forex.target = this.targetCurrency;
		},
		onLotteryTypeChange(e) {
			const index = parseInt(e.detail.value);
			this.lotteryTypeIndex = index;
			// ËΩ¨Êç¢ÂΩ©ÁßçÁ±ªÂûãÔºöÂèåËâ≤ÁêÉ -> ssq, Â§ß‰πêÈÄè -> dlt
			this.lotteryType = index === 0 ? 'ssq' : 'dlt';
		},
		getCurrencyLabel(code) {
			const labels = {
				'CNY': '‰∫∫Ê∞ëÂ∏Å',
				'USD': 'ÁæéÂÖÉ',
				'EUR': 'Ê¨ßÂÖÉ',
				'GBP': 'Ëã±Èïë',
				'JPY': 'Êó•ÂÖÉ',
				'HKD': 'Ê∏ØÂ∏Å',
				'KRW': 'Èü©ÂÖÉ',
				'AUD': 'Êæ≥ÂÖÉ',
				'CAD': 'Âä†ÂÖÉ',
				'SGD': 'Êñ∞Âä†Âù°ÂÖÉ',
				'CHF': 'ÁëûÂ£´Ê≥ïÈÉé',
				'NZD': 'Êñ∞Ë•øÂÖ∞ÂÖÉ',
				'THB': 'Ê≥∞Èì¢',
				'MYR': 'È©¨Êù•Ë•ø‰∫öÊûóÂêâÁâπ',
				'INR': 'Âç∞Â∫¶Âç¢ÊØî'
			};
			return labels[code] || code;
		},
		async queryExpress() {
			if (!this.express.trackingNo) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•Âø´ÈÄíÂçïÂè∑',
					icon: 'none'
				});
				return;
			}
			if (!this.express.phone) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑Á†Å',
					icon: 'none'
				});
				return;
			}
			if (!/^\d{11}$/.test(this.express.phone)) {
				uni.showToast({
					title: 'ÊâãÊú∫Âè∑Á†ÅÂøÖÈ°ª‰∏∫11‰ΩçÊï∞Â≠ó',
					icon: 'none'
				});
				return;
			}

			uni.showLoading({ title: 'Êü•ËØ¢‰∏≠...' });
			try {
				const res = await api.lifeExpress({
					trackingNo: this.express.trackingNo,
					phone: this.express.phone
				});
				if (res.code === 200) {
					this.expressResult = res.data || [];
					if (!this.expressResult || this.expressResult.length === 0) {
						uni.showToast({
							title: 'ÊöÇÊó†Áâ©ÊµÅ‰ø°ÊÅØ',
							icon: 'none'
						});
					}
				} else {
					uni.showToast({
						title: res.message || 'Êü•ËØ¢Â§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				uni.showToast({
					title: 'Êü•ËØ¢Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		async queryOilPrice() {
			uni.showLoading({ title: 'Êü•ËØ¢‰∏≠...' });
			try {
				const res = await api.lifeOilPrice({
					province: this.oil.province || 'Âåó‰∫¨'
				});
				if (res.code === 200) {
					this.oilResult = res.data || {};
					if (!this.oilResult || Object.keys(this.oilResult).length === 0) {
						uni.showToast({
							title: 'ÊöÇÊó†Ê≤π‰ª∑‰ø°ÊÅØ',
							icon: 'none'
						});
					}
				} else {
					uni.showToast({
						title: res.message || 'Êü•ËØ¢Â§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				uni.showToast({
					title: 'Êü•ËØ¢Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		async queryForex() {
			if (!this.forex.amount || parseFloat(this.forex.amount) <= 0) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊúâÊïàÈáëÈ¢ù',
					icon: 'none'
				});
				return;
			}

			uni.showLoading({ title: 'Êü•ËØ¢‰∏≠...' });
			try {
				const res = await api.lifeForex({
					base: this.forex.base,
					target: this.forex.target,
					amount: parseFloat(this.forex.amount)
				});
				if (res.code === 200) {
					this.forexResult = res.data || {};
				} else {
					uni.showToast({
						title: res.message || 'Êü•ËØ¢Â§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				uni.showToast({
					title: 'Êü•ËØ¢Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		getLotteryTypeName(type) {
			const typeMap = {
				'ssq': 'ÂèåËâ≤ÁêÉ',
				'dlt': 'Â§ß‰πêÈÄè',
				'ÂèåËâ≤ÁêÉ': 'ÂèåËâ≤ÁêÉ',
				'Â§ß‰πêÈÄè': 'Â§ß‰πêÈÄè'
			};
			return typeMap[type] || type || 'Êú™Áü•';
		},
		getRedNumbers(numbers, type) {
			if (!numbers) return [];
			// ÂéªÈô§È¶ñÂ∞æÁ©∫Ê†ºÔºåÊåâÁ©∫Ê†ºÊàñÈÄóÂè∑ÂàÜÂâ≤
			const nums = numbers.trim().split(/[\s,]+/).filter(n => n && n.trim());
			if (type === 'ssq' || type === 'ÂèåËâ≤ÁêÉ') {
				// ÂèåËâ≤ÁêÉÔºöÂâç6‰∏™ÊòØÁ∫¢ÁêÉ
				return nums.slice(0, 6);
			} else if (type === 'dlt' || type === 'Â§ß‰πêÈÄè') {
				// Â§ß‰πêÈÄèÔºöÂâç5‰∏™ÊòØÁ∫¢ÁêÉ
				return nums.slice(0, 5);
			}
			return nums;
		},
		getBlueNumbers(numbers, type) {
			if (!numbers) return [];
			// ÂéªÈô§È¶ñÂ∞æÁ©∫Ê†ºÔºåÊåâÁ©∫Ê†ºÊàñÈÄóÂè∑ÂàÜÂâ≤
			const nums = numbers.trim().split(/[\s,]+/).filter(n => n && n.trim());
			if (type === 'ssq' || type === 'ÂèåËâ≤ÁêÉ') {
				// ÂèåËâ≤ÁêÉÔºöÊúÄÂêé1‰∏™ÊòØËìùÁêÉ
				return nums.slice(6);
			} else if (type === 'dlt' || type === 'Â§ß‰πêÈÄè') {
				// Â§ß‰πêÈÄèÔºöÂêé2‰∏™ÊòØËìùÁêÉÔºà‰ªéÁ¨¨6‰∏™ÂºÄÂßãÔºåÂèñ2‰∏™Ôºâ
				return nums.slice(5, 7);
			}
			return [];
		},
		async queryLottery() {
			uni.showLoading({ title: 'Êü•ËØ¢‰∏≠...' });
			try {
				const res = await api.lifeLottery({
					type: this.lotteryType
				});
				if (res.code === 200) {
					this.lotteryResult = res.data || {};
					if (!this.lotteryResult || !this.lotteryResult.issue) {
						uni.showToast({
							title: 'ÊöÇÊó†ÂºÄÂ•ñ‰ø°ÊÅØ',
							icon: 'none'
						});
					}
				} else {
					uni.showToast({
						title: res.message || 'Êü•ËØ¢Â§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				uni.showToast({
					title: 'Êü•ËØ¢Â§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		}
	}
};
</script>

